$header-height: var(--header-height);

.header-nav {
  --header-height: 3vw;

  .grid-x {
    justify-content: space-between;
    align-items: flex-start;
    position: relative;

    & > .cell {
      position: relative;
      z-index: 1;
    }

    &:before {
      content: '';
      display: block;
      position: absolute;
      top: 0;
      left: 100px;
      right: 50px;
      height: $header-height;
      background: none;
      z-index: 0;
    }
  }

  .button-broadcast {
    font-size: var(--fz-24);
    padding: 0 1em;
    line-height: $header-height;
    height: $header-height;
  }
}

.logo {
  background: none no-repeat center / contain;
  display: inline-block;
  //padding: 15px 42px 15px 26px;
  padding: 0.9vw 2.5vw 0.9vw 1.6vw;

  img {
    width: 7.8125vw;
  }

  @include breakpoint(small down) {
    img {
      width: 180px;
    }
  }
}

.menu {
  display: flex;

  a {
    font-size: var(--fz-18);
    line-height: $header-height;
    height: $header-height;
    padding: 0 1em;
    transition: all 0.15s;
    position: relative;
    bottom: 0;
    outline: 0;

    &:after {
      content: "";
      width: 100%;
      height: 100%;
      display: block;
      visibility: hidden;
      position: absolute;
      top: 0;
      left: 0;
      z-index: -1;
      border-radius: 0.5em;
      transform: skewX(-15deg);
      transition: all 0.15s;
    }
  }

  a:hover {
    color: #fff;

    &:after {
      visibility: visible;
      background: get-color(secondary);
    }
  }
}

.socials {
  display: flex;

  a {
    font-size: var(--fz-24);
    line-height: $header-height;
    height: $header-height;
    width: 2em;
    text-align: center;
    transition: all 0.15s;
  }

  a:hover {
    background: get-color(secondary);
  }
}

.header-nav-mobile {
  text-align: center;
  @include hide-for(large up);

  .logo-mobile {
    display: inline-block;
    width: 40vw;
  }
}