.section-03 {
  //background: url(/assets/images/s3/bg.jpg) no-repeat center / cover;
  background: #fff;
  padding: 10vw 0 5vw;

  @include breakpoint(large up) {
    clip-path: polygon(0 0, 100% 7%, 100% 100%, 0 93%);
    margin: -5vw 0 0 0;
  }
}

.section-03 .tabs-items {
  list-style-type: none;
  margin: 0;
  padding: 0;
  display: flex;

  .tabs-item {
    flex: 1 1 auto;
    padding: 0.5em;
  }

  .tabs-item > a {
    @include my-button();
    font-size: var(--fz-18);
    font-weight: bold;
    color: white;
    display: block;
    text-align: center;
    padding: 0.75em 1em;
    text-transform: uppercase;

    &:before {
      background: get-color(primary);
    }
  }

  .tabs-item > a:hover,
  .tabs-item.is-active > a {
    color: #fff;

    &:before {
      background: get-color(secondary);
    }
  }

  @include breakpoint(medium down) {
    flex-wrap: wrap;

    .tabs-item {
      flex: 0 0 50%;
    }
  }
}

.section-03 {
  .tabs-content {
    background-color: transparent;
    border: 1px solid rgb(240, 240, 240);
    margin: 1rem auto 2rem;
  }

  .tabs-panel {
    padding: 0;
  }

  @include breakpoint(medium down) {
    //.tabs-content {
    //  border: 0;
    //  background: none;
    //  clip-path: none;
    //}

    .tabs-panel {
      padding: 5vw 0;
    }
  }
}
