.coef-match-view {
  color: #000;
  position: relative;
  padding: 2em;

  &:before {
    @include absolute-full;
    content: "";
    display: block;
    border: 4px solid #009b3e;
    border-radius: 32px 0;
    //transform: skewX(-15deg);
  }

  // head
  .__head {
    display: flex;
    align-items: start;
    justify-content: center;
  }

  .__date {
    font-family: $ff2;
    font-size: var(--fz-36);
    text-transform: uppercase;
    font-weight: bold;
    margin-right: 0.5ch;
  }

  .__time {
    font-family: $ff2;
    color: #fff;
    background: get-color(secondary);
    font-size: var(--fz-18);
    padding: 0.15em 0.25em;
    border-radius: 4px;
  }


  // teams
  .__teams {
    --sz: 6vw;
    display: flex;
    justify-content: space-between;
    gap: 16px;
    align-items: center;

    @include breakpoint(small down) {
      --sz: 64px
    }
  }

  .__divider {
    font-size: var(--fz-30);
    font-weight: 300;
    font-family: $ff2;
  }

  .__team {
    text-align: center;
    max-width: var(--sz);
  }

  .__team-logo {
    width: var(--sz);
    height: var(--sz);
    object-fit: contain;
    object-position: center;
    margin-bottom: 16px;
  }

  .__team-title {
    font-size: var(--fz-16);
    font-weight: 600;
    font-family: $ff2;
    text-transform: uppercase;
    min-height: 5em;
  }

  // coefs
  .__coefs {
    position: relative;
    margin: 0 -1em -1em;
    padding: 2em 1em;
    border-radius: 32px 0;
    background: #f4f4f4;
  }

  .__coefs-title {
    font-size: var(--fz-16);
    font-family: $ff2;
    text-transform: uppercase;
    letter-spacing: 0.75em;
    text-align: center;
    margin-bottom: 2em;
  }

  .__coefs-list {
    display: flex;
    justify-content: space-between;
    gap: 16px;
  }

  .__coef {
    text-align: center;
    font-family: $ff2;
    font-weight: bold;
    text-transform: uppercase;

    span {
      display: block;
    }

    span:nth-child(1) {
      font-size: var(--fz-36);
      margin-bottom: 0.25em;
    }

    span:nth-child(2) {
      font-size: var(--fz-16);
    }
  }

  .button {
    display: block;
    width: 100%;
    text-align: center;
    font-size: var(--fz-24);
  }
}