.match-view {
  display: grid;
  grid-template-columns: 1fr 2fr auto 2fr 1fr;
  grid-gap: 24px;
  align-items: center;
  color: #000;
  padding: 1vw;

  &:nth-child(even) {
    background: rgb(240, 240, 240);
  }

  @include breakpoint(small down) {
    grid-template-columns: 1fr auto 1fr;
    grid-gap: 16px;
    justify-content: center;
    margin-bottom: 2rem;
    text-align: center;
    padding: 1rem;
  }

  .__time {
    font-family: $ff2;
    font-weight: 300;
    font-size: var(--fz-36);

    @include breakpoint(small down) {
      grid-column: 1/span 3;
    }
  }

  .__team {
    display: flex;
    align-items: center;
    gap: 16px;

    @include breakpoint(small down) {
      gap: 8px;
    }
  }

  .__team-title {
    font-family: $ff2;
    font-weight: 300;
    font-size: var(--fz-36);

    @include breakpoint(small down) {
      font-size: var(--fz-18);
    }
  }

  .__team-logo {
    flex: 0 0 auto;

    img {
      width: 6vw;
      height: 6vw;
      object-fit: contain;
      object-position: center;

      @include breakpoint(small down) {
        width: 60px;
        height: 60px;
      }
    }
  }

  .__team-a {
    justify-content: flex-end;
    text-align: right;
  }

  .__team-b {
    justify-content: flex-end;
    flex-direction: row-reverse;
  }

  .__divider {
    margin: 0 24px;
    font-size: var(--fz-36);

    @include breakpoint(small down) {
      margin: 0;
      font-size: 16px;
    }
  }

  .__ticket {
    text-align: right;

    @include breakpoint(small down) {
      text-align: center;
      grid-column: 1/span 3;
    }
  }

  .button {
    font-size: var(--fz-18);
    white-space: nowrap;
    padding-left: 1em;
    padding-right: 1em;
  }
}