.section-05 {
  padding: 5vw 0;
  background: url(/assets/images/s5/bg.jpg) no-repeat center / cover;
  color: #fff;
  position: relative;

  &:before {
    content: "";
    width: 100%;
    height: 6vw;
    position: absolute;
    top: -3.5vw;
    left: 0;
    background: url(/assets/images/div1.png) no-repeat center / auto 100%;
    pointer-events: none;
  }

  &:after {
    position: absolute;
    bottom: -6vw;
    left: 0;
    z-index: 10;
    content: "";
    width: 100%;
    height: 9vw;
    background: url(/assets/images/div2.png) no-repeat center / cover;
    pointer-events: none;
  }

  .socials {
    justify-content: center;

    a {
      font-size: 4vw;
      color: currentColor;

      @include breakpoint(small down) {
        font-size: 32px;
      }

      &:hover {
        color: currentColor;
        background: none;
        transform: scale(1.1);
      }
    }
  }

  .__text {
    font-family: $ff2;
    font-weight: bold;
    font-size: 4vw;
  }
}