@use "sass:math";

.section-info {
  background: url(/assets/images/s3/bg.jpg) no-repeat center / cover;
  mask-image: url(/assets/images/masks/mask03_2.svg);
  mask-size: cover;
  mask-position: center;
  -webkit-mask-image: url(/assets/images/masks/mask03_2.svg);
  -webkit-mask-size: cover;
  -webkit-mask-position: center;
  color: white;
  padding: 15vw 0 3vw;
  margin-top: -5vw;

  > .grid-container > .grid-x {
    min-height: math.div(630, 1920) * 100vw;
  }


  @include breakpoint(small down) {
    padding: 40vw 1rem 20vw;
    margin-top: -15vw;
  }
}

.section-info .items {
  display: grid;
  grid-gap: 5vw;
  grid-template-columns: 40% 60%;
  grid-template-areas: 'left1 right1' 'left2 right2' 'left3 right3';
  align-items: center;

  .item {
    position: relative;
    align-items: center;

    img {
      width: 100%;
    }
  }

  .item-01 {
    a {
      display: inline-block;
      position: absolute;
      top: 50%;
      left: calc(50% + 2vw);
      font-size: var(--fz-60);
      transform: translate(-50%, -50%);
      transition: color 0.15s;

      &:hover {
        color: var(--color-lightblue);
      }
    }
  }

  .item-02 {
    grid-area: left1;
  }

  .item-04 {
    grid-area: left2;
  }

  .item-06 {
    grid-area: left3;
  }

  .item-01 {
    grid-area: right1;
  }

  .item-03 {
    grid-area: right2;
  }

  .item-05 {
    grid-area: right3;
  }


  @include breakpoint(large up) {
    grid-template-columns: 45% 10% 45%;
    grid-template-rows: 1fr 2fr 1fr 2fr 1fr;
    grid-gap: 1vw;
    align-items: initial;

    .item-01 {
      grid-column: 1/span 2;
      grid-row: 1/span 2;

      img {
        position: relative;
        left: 2.3vw;
      }
    }

    .item-02 {
      grid-column: 3;
      grid-row: 1;
    }

    .item-03 {
      grid-column: 2/span 2;
      grid-row: 2/span 2;
      z-index: 5;
    }

    .item-04 {
      grid-column: 3;
      grid-row: 4;
    }

    .item-05 {
      grid-column: 1/span 2;
      grid-row: 3/span 2;
    }

    .item-06 {
      grid-column: 1/span 3;
      grid-row: 5;
    }
  }

  @include breakpoint(medium down) {
    padding-right: 5vw;

    .fz30 {
      font-size: var(--fz-24);
      font-weight: 100;
    }
  }
}