html, body {
  width: 100%;
  max-width: 100%;
  overflow-x: hidden;
  scroll-behavior: smooth;
}

.margin-bottom-3 {
  @include breakpoint(medium down) {
    margin-bottom: 6vw !important;
  }
}

.grid-container.grid-container {
  max-width: 69vw;

  &.wider {
    max-width: 80vw;
  }

  @include breakpoint(1440px down) {
    max-width: 60vw;

    &.wider {
      max-width: 80vw;
    }
  }

  @include breakpoint(medium down) {
    max-width: 90vw;
    &.wider {
      max-width: 90vw;
    }
  }

  @include breakpoint(small down) {
    max-width: 100vw;
    &.wider {
      max-width: 100vw;
    }
  }
}

