.section-01 {
	background: url(/assets/images/s1/bg.jpg) no-repeat center / cover;
	padding: 10vw 0;
	margin: -5vw 0 -7vw;
	position: relative;

	@include breakpoint(large up) {
		clip-path: polygon(0 8%, 100% 0, 100% 100%, 0 90%);
	}

	mark {
		color: #fff;
		background: $body-font-color;
		display: inline-block;
		padding: 0.25em 0.5em;
	}
}

#standings-table {
	max-width: 960px;
	margin: auto;
	font-family: $ff2;
	font-weight: bold;
	font-style: italic;
	text-transform: uppercase;
	text-align: center;
	font-size: var(--fz-24);

	@include breakpoint(small down) {
		font-size: 12px;
	}

	thead {
		background: get-color(secondary);
		color: #fff;
		border: 2px solid get-color(secondary);

		th {
			text-align: center;

			&:not(:last-child) {
				border-right: 2px solid #fff;
			}
		}
	}

	tr {
		border-bottom: 2px solid get-color(secondary);
	}

	tbody tr {
		background: #fff !important;
	}

	td {
		border: 2px solid get-color(secondary);
	}

