.section-02 {
  padding: 6vw 0;
  background: url(/assets/images/s2/bg.jpg) no-repeat center / cover;
  overflow-x: hidden;
  position: relative;

  @include breakpoint(medium down) {
    padding: 64px 0;
  }

  &:after {
    content: "";
    width: 100%;
    height: 5vw;
    background: url(/assets/images/div1.png) no-repeat center / cover;
    display: block;
    position: absolute;
    top: -2.5vw;
  }
}

#teams-slider {
  .swiper-wrapper {
    align-items: stretch;
  }

  .swiper-slide {
    width: auto;
    height: auto;
  }

  .swiper-slide-active {
    .team-item {
      opacity: 1;

      .__logo {
        filter: none;
        transform: scale(1.15);
      }
      .__title {
        visibility: visible;
      }
    }
  }
}

.team-item {
  width: 12vw;
  opacity: 0.75;
  text-align: center;

  @include breakpoint(small down) {
    width: 150px;
  }

  .__logo {
    width: 9vw;
    height: 9vw;
    object-fit: contain;
    object-position: center;
    //filter: grayscale(100%) sepia(100%) hue-rotate(120deg);
    transform: scale(0.5);
    transition: all 0.25s;

    @include breakpoint(small down) {
      width: 120px;
      height: 120px;
    }
  }

  .__title {
    visibility: hidden;
  }
}