[data-aos-sequence] {
  --delay-step: 100;

  @for $i from 0 through 9 {
	> [data-aos]:nth-child(#{$i}) {
	  transition-delay: calc(var(--delay-step) * #{($i - 1)}ms);
	}
  }
}

// font size classes
@each $size in $font-sizes {
  .fz#{$size} {
	font-size: var(--fz-#{$size});
  }
}

.font-medium {
  font-weight: 500;
}

// COLORS
.color-primary {
  color: get-color(primary);
}

.color-secondary {
  color: get-color(secondary);
}

.color-white {
  color: white;
}

.color-gray {
  color: var(--color-gray);
}

.color-lightblue {
  color: var(--color-lightblue);
}

// fz
@each $n in $font-sizes {
  .fz-#{$n} {
    font-size: var(--fz-#{$n});
  }
}

// fw
.fw-bold {
  font-weight: bold;
}
.fw-300 {
  font-weight: 300;
}