@use "sass:math";

:root {
  --fz-16: #{math.div(16, 1920)*100}vw;
  --fz-18: #{math.div(18, 1920)*100}vw;
  --fz-24: #{math.div(24, 1920)*100}vw;
  --fz-30: #{math.div(30, 1920)*100}vw;
  --fz-36: #{math.div(36, 1920)*100}vw;
  --fz-48: #{math.div(48, 1920)*100}vw;
  --fz-60: #{math.div(60, 1920)*100}vw;
  --fz-72: #{math.div(72, 1920)*100}vw;

  // colors
  --color-lightblue: #0ca1b0;
  --color-gray: #b6b6b6;

  @include breakpoint(medium down) {
    --fz-16: 4vw;
    --fz-18: 4.5vw;
    --fz-24: 6vw;
    --fz-30: 7vw;
    --fz-36: 7vw;
    --fz-48: 7.5vw;
    --fz-60: 8vw;
    --fz-72: 12vw;
  }
}

$font-sizes: [16, 18, 24, 30, 36, 48, 60, 72];
$ff2: 'Oswald', sans-serif;