.section-04 {
  padding: 2vw 0;

  .grid-container {
    @include breakpoint(large up) {
      padding-inline: 10%;
    }
  }

  .logos-grid {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;

    a {
      display: inline-block;
      padding: 1vw;
      width: 33.3333%;
      text-align: center;
    }

    img {
      width: 100%;
    }

    @include breakpoint(medium down) {
      #partner-pm {
        img {
          width: 30vw;
        }
      }
    }
  }
}