@mixin my-button() {
  position: relative;
  display: inline-block;
  padding: 0.25em 2em;
  transition: all 0.15s ease-in-out;

  span {
	position: relative;
  }

  &:before {
	content: '';
	display: block;
	position: absolute;
	top: 0;
	bottom: 0;
	left: 0;
	right: 0;
	z-index: 0;
	border-radius: 1em 0;
	transform: skew(-15deg);
	transition: inherit;
  }
}

@mixin absolute-full() {
	position: absolute;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
}


@mixin font-face( $font-name, $font-path, $font-weight: 400, $font-style: normal, $support-required: 'modern', $font-display: swap) {
  @font-face {
	font-family: $font-name;
	font-style : $font-style;
	font-weight: $font-weight;
	font-display: $font-display;
	@if $support-required=='oldie' {
	  src: url('#{$font-path}.eot');
	  src: url('#{$font-path}.eot?#iefix') format('embedded-opentype'),
	  url('#{$font-path}.woff2') format('woff2'),
	  url('#{$font-path}.woff') format('woff'),
	  url('#{$font-path}.ttf') format('truetype');
	}
	@if $support-required=='recent' {
	  src: url('#{$font-path}.woff2') format('woff2'),
	  url('#{$font-path}.woff') format('woff'),
	  url('#{$font-path}.ttf') format('truetype');
	}
	@if $support-required=='modern' {
	  src: url('#{$font-path}.woff2') format('woff2'),
	  url('#{$font-path}.woff') format('woff');
	}
  }
  @if $support-required !='oldie' and $support-required !='recent' and $support-required !='modern' {
	@error 'Invalid support-required value. Must be "oldie"==>IE8 and above/ Android native browser 4+/ Safari 4.3 OR "recent"==>IE9+/ Android native browser 4.0+/ Safari 4.3+ OR "modern"==>IE9+/ Android native browser 4.4+/ Safari 5.1+';
  }
}