.footer {
  padding: 5vw 0 5em;
  background: #000;
  color: #787878;
  clip-path: polygon(0 0, 100% 8%, 100% 100%, 0 100%);
  margin-top: -1vw;

  @include breakpoint(medium down) {
    text-align: center;
  }

  .footer-logo {
    //width: 10vw;
    //min-width: 150px;
    //filter: grayscale(1);
  }

  .copyrights {
    font-size: var(--fz-16);
    font-weight: 300;
    font-family: $ff2;
  }

  .socials {
    @include breakpoint(small down) {
      justify-content: center;
    }

    a {
      color: #fff;

      &:hover {
        color: get-color(secondary);
        background: transparent;
      }
    }
  }
}